import React, {Component} from 'react'
import {search, getAll} from './BooksAPI'
import Book from './Book'
class BookList extends Component{
state={
    books:[]
}

  componentDidMount(){
  	const booksSite = getAll();
     booksSite.then(books=>(this.setState({books:books})));
    booksSite.then((bs)=>console.log('serverBooks: ',bs))
    .then(()=>console.log('books state:', this.state.books))
  
}
    render(){
        const {books} = this.state;
        return<div className="list-books">
        <div className="list-books-title">
          <h1>MyReads</h1>
        </div>
        <div className="list-books-content">
          <div>
            <div className="bookshelf">
              <h2 className="bookshelf-title">Currently Reading</h2>
              <div className="bookshelf-books">
                <ol className="books-grid">
                {books.map(book=>(<li key={book.id}><Book book={book}/></li>))}

                </ol>
              </div>
            </div>
            </div>
            </div>
            </div>
    }
}
export default BookList;