import React, { Component } from 'react';
import {search} from './BooksAPI';
import Book from './Book';

class SearchBooks extends Component{
state={bookss:[]} 
handleOnChange= (e)=>{
const inputValue = e.target.value;
//this.props.handleOnSearch(inputValue);  
const books = search(inputValue)
books.then(books=>console.log('searchedBooks:', books))
books.then(books=>this.setState({bookss:books}))
.then(()=>console.log('bookss state:',this.state.bookss));
books.then(()=>console.log(Object.keys(this.state.bookss)))


}

render(){
const {bookss} = this.state;

let i = Object.keys(bookss);
let resultBooks = [];
for(let x of i){
    resultBooks=resultBooks.concat([bookss[x]]);
}
//Object.keys(bookss).forEach(key=>{resultBooks=resultBooks.concat([bookss[key]])})
  
const {searchString} = this.props
return(
<div className='search-books-input-wrapper'>
{JSON.stringify(bookss[0])}
<input type='text' className='search-books-bar' value={searchString} onChange={(event)=>this.handleOnChange(event)}/>
<div>
{JSON.stringify(resultBooks)}
</div>
<ol>
{resultBooks.map(book=><li key={book.id}><Book book={book}/></li>)}
</ol>
</div>

);
}
}
export default SearchBooks;